export default {
	data() {
		return {
			isCheckState: 0,
			isbuyType: 0,
			isUrgent: 0,
			selList:[
				{name: '现金'},
				{name: '分期'},
			],
			buyList:[
				{name: '单次采购'},
				{name: '长期采购'},
			],
			urgentList:[
				{name: '紧急'},
				{name: '非紧急'},
			],
			iconsel: "../../static/img/img1.png",
			iconNosel: "../../static/img/img2.png",
		};
	},
	methods: {
		pay(e){
			this.isCheckState = e
		},
		buyType(e){
			this.isbuyType = e
		},
		urgentType(e){
			this.isUrgent = e
		}
	}
};
